import util from '../common/util.js'
export default {
	name:"km-li",
	props:{
		"title":{
			default:''
		},
		"content":{
			default:''
		},
		"isHide":{
			default:false
		},
		"status":{
			default:0
		},
		"isCall":{
			default:false
		},
		"isDate": {
			default: false
		}
	},
	data() {
		return {
			
		};
	},
	computed:{
		hideContent: function(){
			return "******";
		},
		filterStartTime:function() {
			var isToday = util.contrastDate(this.content);
			var str = this.content;
			if(isToday){
				str = '今天'
			} else {
				str = util.formatTime(this.content);
			}
			return str;
		}
	},
	methods:{
		makePhoneCall() {
			uni.makePhoneCall({
				phoneNumber:this.content
			})
		}
	}
}
